import React, {Component} from 'react'
import Main from './Main';
//todo: make svg logo and call it from here
import logo from './logo.svg'
import './App.css'
import {createMuiTheme, MuiThemeProvider} from '@material-ui/core/styles'
import Homepage from './Homepage'
import {Route, Switch} from "react-router-dom"
import NoMatch from "./NoMatch"
import Sandbox from './Sandbox'

const theme = createMuiTheme({
        palette: {
            primary: {
                light: '#9a67ea',
                main: '#ffffff',
                dark: '#320b86',
                contrastText: '#000000'
            },
            secondary: {
                light: '#ffeeff',
                main: '#000000',
                dark: '#c48b9f',
                contrastText: '#ffffff'
            },
        },
        typography: {
            useNextVariants: true,
        },

    }
);

const content = {
    Math: {
        name: 'Math',
        message: '  Our math module will go here it will be comprised of the visualization of a line\n' +
            '                                        integral in 3D space',
        JSONBuild: "webgl/Build/910b5e962d43143a73f762a9487fdce9.json",
        UnityLoader: "webgl/Build/04cfd823429f9d23bbb0a65451607432.js",
    },
    Physics: {
        name: 'Physics',
        message: 'Our physics module will build upon our math module and explain flux using line\n' +
            '                                        integrals.',
        JSONBuild: "Build/Build/Build.json",
        UnityLoader: "Build/Build/UnityLoader.js",
    }
};
const Home = <Homepage content={content}/>;
const Math = <Sandbox content={content.Math}/>;


class App extends Component {


    render() {
        return (

            <MuiThemeProvider theme={theme}>
                {/*<Main/>*/}
                {/*<Homepage/>*/}
                <Switch>
                    <Route exact path='/' component={<Home/>}/>
                    <Route path='/home' render={(props) => <Homepage {...props} content={content}/>}/>
                    <Route exact path={'/' + content.Math.name}
                           render={(props) => <Sandbox {...props} content={content.Math}/>}/>

                    <Route exact path={'/' + content.Physics.name}
                           render={(props) => <Sandbox {...props} content={content.Physics}/>}/>

                    <Route component={NoMatch}/>
                </Switch>
            </MuiThemeProvider>
        );
    }
}

export default App;
